import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { EmailComposer } from '@ionic-native/email-composer';


@IonicPage()
@Component({
  selector: 'page-modalscrawl',
  templateUrl: 'modalscrawl.html',
})
export class ModalscrawlPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,public alertCtrl: AlertController, public emailComposer: EmailComposer) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalscrawlPage');
  }

  retour() {
    this.viewCtrl.dismiss();
  }

  position() {
    const alert = this.alertCtrl.create({
      title: 'Où nous trouvez ?',
      subTitle: 'Pour le moment, nous sommes situés à l\'INPHB de Yamoussoukro en Côte d\'Ivoire.',
      buttons: ['D\'accord']
    });
    alert.present();
  }

  mail() {
    //this.emailComposer.addAlias('gmail', 'com.google.ios.gm');
    let email = {
      to: 'yannsid2@gmail.com',
      subject: 'Suggestion',
      body: 'Félicitations pour votre superbe application : e-Muslim',
      isHtml: false,
      app: 'gmail'
    };
    this.emailComposer.open(email);
  }
}



